html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  background-color: var(--petal);
  color: var(--blackberry);
  font-family: var(--fontBody);
}

h1,
h2,
h3 {
  font-family: "Playfair Display", serif;
  font-weight: 800;
  line-height: 1.2;
}

a,
a:visited,
a:active {
  color: var(--blackberry);
  text-decoration: none;
  display: inline-block;
}

a:hover {
  color: var(--pinkGuava);
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}

object {
  pointer-events: none;
  display: block;
}

address {
  all: inherit;
}

.border {
  border: 1px solid lightslategray;
}

.b-p {
  border: 1px solid pink;
}

.b-r {
  border: 1px solid orangered;
}

.b-b {
  border: 1px solid lightskyblue;
}

.b-y {
  border: 1px solid gold;
}

.b-g {
  border: 1px solid greenyellow;
}

.hide {
  display: none;
}

.invisible {
  visibility: hidden;
}

.body-width {
  width: 100vw;
}

.content-width {
  width: 86vw;
}

.content-height {
  height: 100vh;
}

.grid {
  display: grid;
}

.grid--place-center {
  place-items: center;
}

.grid--align-c-center {
  align-content: center;
}

.grid--align-c-end {
  align-content: end;
}

.grid--align-c-between {
  align-content: space-between;
}

.grid--just-i-right {
  justify-items: right;
}

.flex--just-apart {
  justify-content: space-between;
}

.flex {
  display: flex;
}

.flex-align-end {
  align-items: flex-end;
}

.flex-baseline {
  align-self: baseline;
}

.upper {
  text-transform: uppercase;
}

:root {
  --blackberry: hsl(266, 8%, 18%);
  --rusty: hsl(24, 74%, 51%);
  --orange: #FF7F50;
  --brightOrange:hsl(11, 80%, 60%);
  --brightRedPink: hsl(343, 100%, 39%);
  --brightPink: #DE3163;
  --pinkGuava: hsl(354, 63%, 60%);
  --peach: hsl(6.2,87.9%,93.5%);
  --petal: hsl(43, 100%, 99%);
  --shadow: rgba(45, 42, 49, 0.5);
}

:root {
  --fontDisplay: "Playfair Display", serif;
  --fontBody: "Montserrat", sans-serif;
  --font-body-sm: 14px;
  --font-body-m: 16px;
  --font-body-l: 18px;
  --font-subtitle-mob: 20px;
  --font-subtitle-desk: 24px;
  --font-title-mob: 28px;
  --font-title-desk: 32px;
  --font-display-mob: 34px;
  --font-display-desk: 64px;
}

button {
  font-family: inherit;
  /* For all browsers */
  line-height: 1.15;
  /* For all browsers */
  margin: 0;
  /* Firefox and Safari have margin */
  overflow: visible;
  /* Edge hides overflow */
  text-transform: none;
  /* Firefox inherits text-transform */
  -webkit-appearance: button;
  /* Safari otherwise prevents some styles */
}

button::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring {
  outline: 1px dotted ButtonText;
}

.btn {
  font-family: inherit;
  font-size: 0.75rem;
  border: 0;
  border-radius: 3.125rem;
  background: linear-gradient(90deg, var(--brightOrange) 0%, var(--brightPink) 100%);
  color: var(--petal) !important;
  line-height: 1.2;
  letter-spacing: 1px;
  white-space: nowrap;
  text-decoration: none;
  padding: 0.875rem 1.75rem;
  cursor: pointer;
  transform: translateY(0);
  transition: transform 150ms, box-shadow 150ms;
}

.btn:hover {
  color: var(--petal);
  transform: translateY(-1px);
  box-shadow: rgba(45, 42, 49, 0.1) 0px 4px 6px -1px, rgba(45, 42, 49, 0.06) 0px 2px 4px -1px;
}

.btn-talk {
  margin-top: 1.25rem;
  display: inline-block;
}

.btn-view {
  justify-self: center;
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
  display: inline-block;
}

.header {
  height: 2.5rem;
}

.logomark {
  width: 1.125rem;
}

.menu-icon-moblie {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  cursor: pointer;
  position: relative;
  z-index: 9;
}
.menu-icon-moblie > span {
  display: block;
  width: 1.375rem;
  height: 0.125rem;
  background-color: var(--blackberry);
  transition: all 300ms ease-in-out;
}
.menu-icon-moblie > span:first-child {
  margin-bottom: 0.375rem;
}
.menu-icon-moblie > span:last-child {
  width: 1rem;
}

.menu-toggle > span:first-child {
  transform: rotate(45deg);
}
.menu-toggle > span:last-child {
  width: 1.375rem;
  transform: translateY(-8px) rotate(-45deg);
}

.open {
  visibility: visible !important;
  opacity: 0.95 !important;
}

.overlay-menu-mobile {
  text-align: center;
  visibility: hidden;
  background-color: var(--peach);
  transform: translateY(0);
  transition: all 0.375s ease-in-out;
  opacity: 0;
  width: 100%;
  height: 100%;
  position: fixed;
  overflow: auto;
  top: 0;
  left: 0;
  z-index: 1;
  padding: 3.125rem 1.5625rem;
}
.overlay-menu-mobile > li {
  padding: 1.25rem 0;
}

.ul-desktop > li {
  margin-left: 2.25rem;
}

@media (max-width: 48em) {
  .menu-desktop {
    display: none;
  }
}
@media (min-width: 48.0625em) {
  .menu-icon-moblie {
    display: none;
  }
}
.landing {
  height: 80vh;
  justify-self: center;
}

.landing__img-container {
  min-height: 36vh;
  place-items: center;
}

.landing__svg {
  width: 11.75rem;
  transform: translate(10%, 20%);
}

.landing__info-container {
  text-align: center;
  align-content: space-between;
}

.landing__info--title {
  font-size: var(--font-display-mob);
}

.landing__info--body {
  font-size: var(--font-body-m);
  line-height: 1.5;
}

.btn-talk {
  justify-self: center;
}

@media (min-width: 58em) {
  .landing {
    grid-template-columns: 3fr 2fr;
    grid-template-areas: "left right";
    gap: 1.25rem;
    width: 70vw;
  }

  .landing__img-container {
    grid-area: right;
  }

  .landing__svg {
    width: 17.5rem;
    transform: translateY(-25%);
  }

  .landing__info-container {
    text-align: left;
    align-content: center;
  }

  .landing__info--title {
    font-size: var(--font-display-desk);
  }

  .landing__info--body {
    font-size: var(--font-body-l);
    line-height: 1.7;
  }

  .btn-talk {
    justify-self: start;
  }
}
.work {
  background-image: url("../../../assets/work-bkg-big.svg");
  background-size: cover;
  background-repeat: no-repeat;
  width: 100vw;
  height: 700px;
}

.work-icon {
  width: 8.75rem;
}